<template>
  <div class="user-wrap">
    <van-nav-bar
      title="寄售"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-field
      readonly
      clickable
      label="一级分类"
      :value="type1"
      placeholder="选择一级分类"
      @click="showPicker1 = true"
      input-align="right"
    />
    <van-field
      readonly
      clickable
      label="二级分类"
      :value="type2"
      placeholder="选择二级分类"
      @click="showPicker2 = true"
      input-align="right"
    />
    <van-field
      readonly
      clickable
      label="商品"
      :value="type3"
      placeholder="选择商品"
      @click="showPicker3 = true"
      input-align="right"
    />
    <van-field 
      label="价格"
      v-model="price" 
      error
      disabled 
      input-align="right"
    />
    <van-notice-bar :scrollable="false">
      <p class="small-word">
        当前可寄售数量: {{canSell}}
      </p>
    </van-notice-bar>
    <van-divider></van-divider>
    <div class="quan-contain" v-if="upType.indexOf(2) >= 0">
      <van-field name="uploader" label="">
        <template #input>
          <van-uploader v-model="uploader" style="margin-left: 40%;"/>
        </template>
      </van-field>
      <van-notice-bar>
        <p class="small-word">上传券码截屏/压缩包，要求图片有完整条码或二维码</p>
      </van-notice-bar>
    </div>
    <div class="quan-contain" v-if="upType.indexOf(1) >= 0">
      <van-field
        v-model="quanma"
        rows="4"
        autosize
        label=""
        type="textarea"
        maxlength="500"
        placeholder="卡号和密码必须在同一行，用逗号隔开。多组卡密用回车键分开。"
        clearable
        show-word-limit
        input-align="left"
      />
    </div>
    <van-field
      v-model="source"
      label=""
      rows="2"
      autosize
      type="textarea"
      maxlength="100"
      placeholder="3天内到期券码必须注明有效期、非指定活动券码注明来源"
      clearable
      show-word-limit
      input-align="left"
    />
    <div class="tips">
      <p class="title small-word">来源及提示</p>
      <p class="small-word">来源</p>
      <div class="content">
        <p class="small-word">
          *华夏银行信用卡公众号-活动地带-权益平台-活动。领取星巴克大杯电子券  交通银行:买单吧APP直接搜“星巴克”，21000积分兑换，多余积分可以继续兑换
        </p>
        <p class="small-word">
          *浦发信用卡~小浦惠花~定位上海/其他大城市，积分专享星巴克中杯 每日11:00~24:00可抢 每人每周限购2张@所有人
        </p>
        <p class="small-word">
          *招商掌上生活app，饭票，9分招牌菜，799积分兑换星巴克中杯，只要当月的货，交单 截屏➕复制券码@所有人
        </p>
        <p class="small-word">
          *招商掌上生活有星巴克6/8元优惠券的朋友，购买大杯美式/拿铁，随机减至少2@所有人
        </p>
        <p class="small-word">
          *招商扫码抽奖，配合立减可以买拿铁/美式
        </p>
        <p class="small-word">
          <img :src="img" alt="">
        </p>
        <p class="small-word">
          *招商饭票买拿铁~招商APP~怦然星动~大杯拿铁，原价32，其他途径的拿铁也收@所有人
        </p>
        <p class="small-word">
          *招商银行app，生活，中间滚动条，免费领15元美食红包，进去领饭票，买星巴克大杯拿铁，随机减看人品大毛@所有人
        </p>
        <p class="small-word">
          *招商掌上生活3/29领取星巴克8元优惠券的朋友，购买大杯美式/拿铁，随机减至少2，付款18以下，截图二维码+券尾4个号给我@所有人
        </p>
        <p class="small-word">
          *邮储信用卡用户报名成功及刷卡达标，会收到邮储中杯短信 报名渠道：邮储银行信用卡微信~鼎 账号~我的账户~账单查询~我的积分~微商城~首页活动轮播图@所有人
        </p>
        <p class="small-word">
          *限工商借记卡：支付宝APP，口碑点进去随便进一个商品界面，领取10元券，然后搜星巴克，买大杯拿铁/美式
        </p>
        <p class="small-word">
          每周五交行星巴克55-20
        </p>
        <p class="small-word">
          *每周五支付宝口碑~星巴克任意店~买2个大杯拿铁，选择交通信用卡付款，下周三领取20刷卡金，没有领到，申请退款，要保险起见的可以领了刷卡金后发我，交单 截屏+复制券码@所有人
        </p>
      </div>
      <p class="small-word">提示：</p>
      <div class="content">
        <p class="small-word">
          *只收一手券，核实不到券码的使用情况的提供不了凭证的有纠纷自行承当。
        </p>
        <p class="small-word">
          *请确保券码有效期大于10天。
        </p>
        <p class="small-word">
          *若上传的串码已被使用或为错误串码，即便您已拿到货款，后续也会被追责，故请卖家认真核实。
        </p>
      </div>
    </div>
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="客服" @click="goPage('/qscode', 1)" />
      <van-goods-action-icon icon="star-o" text="收藏" />
      <van-goods-action-button type="danger" text="提交" color="#07c160" @click="onSubmit"/>
    </van-goods-action>
    <van-popup v-model="showPicker1" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns1"
        @cancel="showPicker1 = false"
        @confirm="onConfirm1"
      />
    </van-popup>
    <van-popup v-model="showPicker2" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns2"
        @cancel="showPicker2 = false"
        @confirm="onConfirm2"
      />
    </van-popup>
    <van-popup v-model="showPicker3" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns3"
        @cancel="showPicker3 = false"
        @confirm="onConfirm3"
      />
    </van-popup>
  </div>
</template>

<script>
export default {
  name: 'parchase',
  components: {
  },
  data () {
    return {
      upType: [1,2],
      type1: '',
      type2: '',
      type3: '',
      price: '¥2131',
      canSell: 32,
      source: '',
      active: 0,
      showPicker1: false,
      showPicker2: false,
      showPicker3: false,
      columns1: ['1a', '1b', '1c', '1d', '1e'],
      columns2: ['2a', '2b', '2c', '2d', '2e'],
      columns3: ['3a', '3b', '3c', '3d', '3e'],
      quanma: '',
      uploader: [],
      img: require('../assets/images/qscode.jpg'),
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    onConfirm1(value) {
      this.type1 = value
      this.showPicker1 = false
    },
    onConfirm2(value) {
      this.type2 = value
      this.showPicker2 = false
    },
    onConfirm3(value) {
      this.type3 = value
      this.showPicker3 = false
    },
    onClickLeft () {
      this.$router.go(-1)
    },
    onSubmit () {
      console.log('submit')
    },
    goPage (url, type) {
      this.$router.push({ path: url, query: {type: type} })
    }
  }
}
</script>

<style scoped lang="less">
  .quan-contain {
    padding: 10px 0;
  }

  .tips {
    padding: 10px 15px;
    color: #666;
    
    .content {
      // max-height: 100px;
      // overflow-y: auto;
      margin-bottom: 10px;
    }

    p {
      &.title {
        color: #333;
        line-height: 14px;
        padding-left: 6px;
        border-left: 3px solid red;
        margin-bottom: 10px;
      }

      line-height: 20px;
    }
  }
</style>


